.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}


body {
  margin: 40px;
}

.sidebar {
  grid-area: sidebar;
}

.content {
  grid-area: content;
  height: 750px;
  background: url(assets/images/welcome-background.jpg) bottom center no-repeat;
  background-size: cover;
}

.header {
  grid-area: header;
}

.footer {
  grid-area: footer;
}


.wrapper {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 100%;
  grid-template-areas:
    "header"
    "content"
    "footer";
  background-color: #fff;
  color: #00405c
}


.box {
  background-color: #00405c;
  color: #fff;
  border-radius: 5px;
  padding: 10px;
  font-size: 150%;
}

.footer {
  /* position: sticky; */
  bottom: 0;
}

.menu-list .menu {
  list-style-type: none;
  float: left;
  margin-left: 30px;
}


.header {
  color: #fff;
  /* position: sticky; */
  top: 0;
}

.mdl {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  visibility: hidden;
}

.mdl.show {
  visibility: visible;
}

.mdl-mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
}

.mdl .sidebar {
  position: absolute;
  background-color: #fff;
  border-left: 1px solid #ccc;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
  top: 0;
  right: 0;
  bottom: 0;
  width: 350px;
  transform: translateX(350px);
  padding: 20px;
  transition: transform 0.3s;
}

.mdl.show .sidebar {
  transform: translateX(0);
}

.small-margin-right {
  margin-right: 5px;
}